import axios from "axios";
import JWT from "./jwt";
import Data from "../core/data";
// Server url to save call back info
const CallBack_Server = "https://api.${region}.500apps.com/pbxplus/v1/contact-info?app_name=pbxplus";

class CallBack extends Data {
  // Method used to send call back info
  async add(form) {
    let headers = await JWT.setHeader();
    headers["x-api-key"] = form.api_key;
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = CallBack_Server;
    url = eval("`" + url + "`");
    let response = await axios.post(url, form, { headers });
    return response.data;
  }
}
export default new CallBack();
