<template>
  <div class="width-400">
    <b-card body-class="p-3" class="visitor-callback mb-0">
      <b-form ref="form" @submit.prevent="onSubmit">
        <b-form-group label="Enter Name" label-for="input-name" class="mb-3" size="sm">
          <b-form-input
            size="sm"
            type="text"
            oninvalid="this.setCustomValidity('Please enter proper name')"
            oninput="this.setCustomValidity('')"
            v-model="form.name"
            id="input-name"
            placeholder="Enter Name"
            pattern="[a-zA-Z ]{3,30}"
            required
          ></b-form-input>
        </b-form-group>
        <b-form-group label="Enter Phone Number" class="mb-3" size="sm">
          <VueTelInput v-model="form.number" v-on:country-changed="countryChanged" required size="sm" />
        </b-form-group>
        <b-form-group label="Call Now/Schedule this later" class="mb-3" size="sm">
          <b-form-select size="sm" v-model="form.send_type" :options="sendType" class="mb-0" required></b-form-select>
        </b-form-group>
        <b-row v-if="form.send_type == 'Scheduled'">
          <b-col md="12" size="sm">
            <b-form-group label="Callback Time" class="mb-3" size="sm">
              <b-form-input type="datetime-local" size="sm" class="form-control pr-2" v-model="form.call_back_time" id="callbacktime" required></b-form-input>
            </b-form-group>
          </b-col>
          <b-col lg="12">
            <label size="sm">Timezone</label>
            <b-form-select size="sm" v-model="form.timezone" :options="getTimezones()" required />
          </b-col>
        </b-row>
        <div class="d-flex justify-content-end border-top mt-3 pt-3">
          <b-button size="sm" block type="submit" variant="primary px-5" :disabled="isDisabled">Submit</b-button>
        </div>
        <p :class="errorClass" class="mb-0 text-right mt-2 text-primary">{{ message }}</p>
      </b-form>
    </b-card>
  </div>
</template>

<script>
  import { VueTelInput } from "vue-tel-input";
  import call from "../../js/dialer/call-back";
  import moment from "moment-timezone";
  export default {
    components: { VueTelInput },
    props: ["domain_id", "api_key", "call_flow_id"],
    data() {
      return {
        form: {
          name: "",
          number: "",
          timezone: "",
          api_key: "",
          call_flow_id: "",
          domain_id: "",
          message: "",
          call_back_time: "",
          send_type: "Call now",
          country_code: "",
        },
        isDisabled: false,
        errorClass: "",
        message: "",
        sendType: [
          {
            value: "Call now",
            text: "Call now",
          },
          {
            value: "Scheduled",
            text: "Schedule later",
          },
        ],
      };
    },
    async mounted() {
      // To get today's date
      this.form.timezone = moment.tz.guess();
    },
    methods: {
      countryChanged(country) {
        this.form.country_code = country.dialCode;
      },
      getTimezones() {
        let zones = moment.tz.names();
        zones.unshift({ text: "Select timezone", value: "" });
        return zones;
      },
      async onSubmit() {
        this.form.domain_id = this.domain_id;
        this.form.call_flow_id = this.call_flow_id;
        this.form.api_key = this.api_key;
        this.message = "saving..";
        this.isDisabled = false;

        try {
          // Store callback data
          await call.add(this.form);
          this.message = "Saved your data successfully";
          this.errorClass = "text-primary";
        } catch (err) {
          if (err.response.data.error.includes("Duplicate entry")) {
            this.message = "Data already exists";
            this.errorClass = "text-danger";
          } else {
            this.message = "Unable to save, Please try again";
            this.errorClass = "text-danger";
          }
        }
        setTimeout(() => {
          this.message = "";
        }, 2000);
      },
    },
  };
</script>
